import { Component, OnInit, NgZone } from '@angular/core';
import { PedidoStore } from "app/almacen/pedido.store";
import { UsersService } from "app/users.service";
import { Order } from "app/order";
import { Router } from "@angular/router";

declare var HybridBridge: any;

@Component({
  selector: 'app-form-order',
  templateUrl: './form-order.component.html',
  styleUrls: ['./form-order.component.css']
})
export class FormOrderComponent implements OnInit {

  public date: string;
  public name: string;
  public tienda: string;

  constructor(
    private ngZone: NgZone,
     private usersService: UsersService,
     private pedidoStore: PedidoStore,
     private router: Router
    ) { }

  ngOnInit() {
  }

  public seleccionar(): void {
    HybridBridge.obtenerTienda((tienda) => {
      this.ngZone.run(() => {
        this.tienda = tienda;
      });
    });
  }

  public enviar(fecha: string, nombre: string) {
    console.log(fecha);
    console.log(nombre);
    console.log(this.tienda);

    var order = new Order();
    
    order.codPedido =  (Math.random() * (100000 - 1) + 1).toString();
    order.codTienda = nombre + (Math.random() * (1000 - 1) + 1).toString();
    var fechaEntrega = new Date(fecha);
    
    order.fechaEntrega = fechaEntrega;
    order.imagenes = this.pedidoStore.imagenes.join('|');
    order.total =  this.pedidoStore.imagenes.length * 0.20;

    debugger;
    console.log(this.usersService.order(order)
        .then( result => {
          debugger;
          alert("Pedido realizado con éxito");
          this.router.navigateByUrl('home', {replaceUrl: true});
              
        })
        .catch( e => {
          debugger;
          console.log(e);
        }));
    
  }
}



