import { Injectable } from '@angular/core';
import { RequestOptions, Headers, Http } from '@angular/http';

import { User } from './user';
import { Settings } from '../settings';

import 'rxjs/add/operator/toPromise';
import { PedidoStore } from "app/almacen/pedido.store";
import { Order } from "app/order";
import { ParametrosStore } from "app/almacen/parametros.store";

@Injectable()
export class UsersService {

  constructor(
    private http: Http,    
    private parametrosStore: ParametrosStore,
  ) { }

  public register(user: User): Promise<any> {
    console.log(user);
    return this.http.post(Settings.WS_URL + 'koprint/users', user)
      .toPromise();
  }

  public order(order: Order): Promise<any>{
    return this.parametrosStore.get('accessToken')
        .then (result => {

          var options: RequestOptions = new RequestOptions({
            headers: new Headers({
              'Authorization': 'Bearer '+ result
            })
          });
          console.log(result);
          console.log(order);
          return this.http.post(Settings.WS_URL + 'koprint/orders', order, options)
              .toPromise();  
              
        });

  }

  public findAllUsers(): Promise<any> {

    var options: RequestOptions = new RequestOptions({
      headers: new Headers({
        'Authorization': 'Bearer 96ad7aae0b1a531d04eb806ecb2adb2d3a83de3f'
      })
    });

    return this.http.get(Settings.WS_URL + 'users', options)
      .toPromise();
  }

  public login(user: User): Promise<any> {

    var options: RequestOptions = new RequestOptions({
      headers: new Headers({
        'Authorization': 'Basic Q2xpZW50ZS1Db3Jkb3ZhOkNsaWVudGUtQ29yZG92YQ==',
        'Content-Type': 'application/x-www-form-urlencoded'
      })
    });

    return this.http.post(Settings.WS_URL + 'oauth/token',
      'grant_type=password&username=' + user.email + '&password=' + user.clave,
      options)
      .toPromise();
  }
}



