import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from "@angular/router";
import { ParametrosStore } from "app/almacen/parametros.store";
import { StatusService } from "app/status.service";

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

  public logged: boolean = false;
  public title: string = 'Koprint';

  @ViewChild('mdlLayout')
  private mdlLayout: any;

  constructor(
    private router: Router,
    private parametrosStore: ParametrosStore,
    private statusService: StatusService
  ) {
    this.statusService.logged.subscribe(logged => this.logged = logged);
    this.statusService.title.subscribe(title => this.title = title);
  }

  ngOnInit() {
  }

  public logout(): void {
   
    this.statusService.logged.next(false);
    this.parametrosStore.set('accessToken', null);
    this.router.navigate(['login'], {replaceUrl: true});
    this.mdlLayout.nativeElement.MaterialLayout.toggleDrawer();
  }

  public irAcercaDe(): void{
    this.router.navigate(['acercaDe']);
    this.mdlLayout.nativeElement.MaterialLayout.toggleDrawer();
  }

}



